@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
header{
    background-color: white;
    width: 100%;
    height: 4rem;
    display: grid;
    align-content: center ;
    text-transform: uppercase;
}

h1 {
        color: #844A1B;
        text-align: center;
        font-size: 3.5rem;
        font-weight: lighter;
}

.terramoto{
    background-color: white;
    text-align: center;
    padding: 1.5rem;
    border-radius: 1rem;
    line-height: 2rem;
    box-shadow: 0 0 0pt 2.5pt #844A1B;

margin-left: 2rem;
margin-right: 2rem;
margin-bottom: 2rem;
}

.info h2{
    color: #844A1B;
}
main {
    padding: 4rem 1rem 1rem 1rem;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1rem;
}

@media screen and (max-width: 769px) {
    main a{
        grid-column: span 3;
    }
}
.detalhe{
    grid-column: span 3;
}

main#terramoto a{
    grid-column: span 3;
    background-color: white;
    padding: .4rem;
    border-radius: 1rem;
    line-height: 2rem;
    box-shadow: 0 0 0pt 2.5pt #844A1B;
    margin-left: 2rem;
    margin-right: 2rem;

}


.btn-home{
    color: #844A1B;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
}


*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

:root{
    font-size: 1vw;
    line-height: 1.1em ;
}

html, body{
    width: 100%;
    height:100%;
    font-family: 'Montserrat', sans-serif;
    background-color: #C4C4C4;
}

a{
    text-decoration: none;
}


@media screen and (max-width: 769px) {
    :root{
        font-size: 2.1vw;
        line-height: 1.1em ;
    }
    
}

@media screen and (max-width: 577px) {
    :root{
        font-size: 4vw;
        line-height: 1.1em ;
    }
    
}


